import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Menu,
  Icon,
  Input,
  Label,
  Dropdown,
  Button,
  Segment,
  Image,
} from 'semantic-ui-react'
import {
  toggleUnit,
  toggleMenu,
  searchCountriesByName,
  sortCountries,
  filterCountriesByRegion,
  filterCountriesBySubRegion,
} from '../reducers/countryReducer'
import regions from '../regions'

import '../assets/css/App.css'
import flag from '../assets/images/ua.svg'

const HeaderNav = () => {
  const [value, setValue] = useState('')
  const [activeItem, setActiveItem] = useState('All')
  const [activeSubregion, setActiveSubregion] = useState({ name: 'Select' })
  const [region, setRegion] = useState({
    id: 'FZUe47mEY9PCOzYmMxzYY',
    region: 'All',
    subregions: [],
  })

  const state = useSelector((state) => state)

  const recordsFound = useSelector((state) => state.countries.filtered)

  const unit = useSelector((state) => state.unit.unit)

  const dispatch = useDispatch()

  useEffect(() => {
    if (value !== '') {
      setRegion({
        id: 'FZUe47mEY9PCOzYmMxzYY',
        region: 'All',
        subregions: [],
      })
      setActiveItem('All')
    }
  }, [value])

  const handleInput = (e) => {
    e.preventDefault()
    const input = e.target.value
    dispatch(searchCountriesByName(input))
    setValue(input)
  }

  const clearInput = () => {
    dispatch(searchCountriesByName(''))
    setValue('')
  }

  const showHideMenu = () => {
    dispatch(toggleMenu())
  }

  const changeUnit = (val) => {
    dispatch(toggleUnit(val))
  }

  const handleSort = (key) => {
    dispatch(sortCountries(key))
  }

  const handleRegionClick = (reg) => {
    dispatch(filterCountriesByRegion(reg.region))
    setActiveItem(reg.region)
    if (reg.region === 'All') {
      setActiveSubregion({ name: 'Select' })
    }
    setRegion(reg)
    setValue('')
  }

  const handleSubregionClick = (sub) => {
    dispatch(filterCountriesBySubRegion(sub))
    setActiveSubregion({ name: sub })
    setValue('')
  }

  const handleSubClose = () => {
    setActiveSubregion({ name: 'Select' })
    dispatch(filterCountriesByRegion(region.region))
  }

  return (
    <>
      <Menu attached="top" inverted borderless>
        <Menu.Item id="app-title" header onClick={() => showHideMenu()}>
          <p> World Countries</p>
        </Menu.Item>
        <Menu.Item id="flag-menu">
          <Image
            alt="ukraine flag"
            srcSet={`${flag} 100w`}
            size="tiny"
            src={flag}
            as="a"
            href="https://supportukrainenow.org/"
          />
        </Menu.Item>
        <Menu.Menu position="right">
          <Menu.Item
            onClick={() =>
              changeUnit(unit === 'metric' ? 'imperial' : 'metric')
            }
          >
            <Label color="blue">
              {unit === 'metric' ? 'imperial' : 'metric'}
            </Label>
          </Menu.Item>
        </Menu.Menu>
      </Menu>

      {/* text input */}
      <Menu id="mobile-header" inverted vertical fluid>
        <Menu.Item id="mobile-header-item">
          <Input
            inverted
            size="mini"
            icon={
              <Icon
                link
                name={value.length > 0 ? 'close' : 'search'}
                onClick={value.length > 0 ? clearInput : handleInput}
              />
            }
            type="search"
            value={value}
            placeholder="Search by name"
            onChange={handleInput}
          />
        </Menu.Item>
      </Menu>
      <Menu id="mobile-header2" inverted vertical fluid>
        <Menu.Item id="mobile-header2-item">
          {recordsFound.length}
          {recordsFound.length === 1 ? ' record found' : ' records found'}
        </Menu.Item>
      </Menu>

      {/* sort menu */}
      {recordsFound.length === 1 ? (
        <>
          <Segment inverted>
            <Button
              labelPosition="left"
              icon="left chevron"
              basic
              color="yellow"
              fluid
              content="All Countries"
              onClick={() => dispatch(searchCountriesByName(''))}
            ></Button>
          </Segment>
        </>
      ) : (
        <>
          <Menu inverted borderless size="tiny">
            <Dropdown item inline text="Sort" defaultValue="Name">
              <Dropdown.Menu id="dropdown menu">
                <Dropdown.Item onClick={() => handleSort('name')}>
                  {state.countries.sortConfig.key === 'name' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Name
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('capital')}>
                  {state.countries.sortConfig.key === 'capital' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Capital
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('region')}>
                  {state.countries.sortConfig.key === 'region' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Region
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('subregion')}>
                  {state.countries.sortConfig.key === 'subregion' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Subregion
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('population')}>
                  {state.countries.sortConfig.key === 'population' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Population
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('area')}>
                  {state.countries.sortConfig.key === 'area' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Area
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>

            <Menu.Item position="right">
              Sorted by: {state.countries.sortConfig.key}
              {', '}
              {state.countries.sortConfig.direction}
            </Menu.Item>
          </Menu>

          <Menu inverted borderless size="tiny">
            <Dropdown item inline text="Filter">
              <Dropdown.Menu>
                {regions.map((r) => (
                  <Dropdown.Item
                    key={r.id}
                    text={r.region}
                    value={r.region}
                    active={activeItem === r.region}
                    onClick={() => handleRegionClick(r)}
                  />
                ))}
              </Dropdown.Menu>
            </Dropdown>
            <Menu.Item position="right">
              Filtered by: {activeItem}
              {/*   {activeSubregion.name !== 'Select'
                ? `, ${activeSubregion.name}`
                : ''} */}
            </Menu.Item>
          </Menu>
          {region.region !== 'All' ? (
            <Menu inverted borderless size="tiny">
              <Dropdown item inline text="Subregion">
                <Dropdown.Menu>
                  {region.subregions.map((s) => (
                    <Dropdown.Item
                      key={s.id}
                      text={s.name}
                      value={s.name}
                      active={activeSubregion.name === s.name}
                      onClick={() => handleSubregionClick(s.name)}
                    />
                  ))}
                </Dropdown.Menu>
              </Dropdown>
              <Menu.Item position="right">
                {activeSubregion.name !== 'Select' ? (
                  <Label>
                    {activeSubregion.name}
                    <Icon onClick={() => handleSubClose()} name="delete" />{' '}
                  </Label>
                ) : (
                  ''
                )}
              </Menu.Item>
            </Menu>
          ) : (
            <></>
          )}
        </>
      )}
    </>
  )
}

export default HeaderNav
